body {
    background-color: #013b0e;
    background-image: url(/solitaire/static/media/felt.5b148bae.png);
    /*https://www.transparenttextures.com/felt.html This is mostly intended for prototyping; please download the pattern and re-host for production environments. Thank you! */
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.align-left,
.align-right {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
}

.align-left {
    left: 0;
}

.align-right {
    right: 0;
}

.align-center {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.ui {
    z-index: 2000;
}

.ui > .title {
    font-size: 2vw;
    margin-bottom: 2vw;
}

.ui .closer {
    position: absolute;
    right: 0.3vw;
    top: 0.1vw;
}

.ui .closer button {
    font-size: 2vw;
    padding-top: 0vw;
    border-color: transparent;
    border-style: none;
    width: 3vw;
    margin-top: 0vw;
    color: white;
    background-color: transparent;
}

.ui .closer button:focus {
    outline-color: gray;
    outline-width: 0.05vw;
}

.ui.left {
    position: absolute;
    border-radius: 0.5vw;
    z-index: 6000;
    background-color: rgba(0, 0, 0, 0.95);
    color: white;
    left: 2vw;
    padding: 2vw;
    width: 25vw;
}

.ui.right {
    position: absolute;
    border-radius: 0.5vw;
    z-index: 6000;
    background-color: rgba(0, 0, 0, 0.95);
    color: white;
    right: 2vw;
    padding: 2vw;
    width: 25vw;
}

.ui > .content > .section {
    font-size: 1vw;
    margin-bottom: 8vw;
}

.ui > .content > .section:last-child {
    margin-bottom: 0vw;
}

.ui > .content > .section > .title {
    font-size: 1.5vw;
    width: 25vw;
    float: left;
}

.ui > .content > .section > .row > select {
    float: right;
    margin-left: 2vw;
    width: 8vw;
    font-size: 1vw;
}

.ui > .content > .section > .row > .label {
    float: left;
    width: 15vw;
    font-size: 1w;
    color: lightgray;
}

.ui > .content > .section > .row {
    clear: both;
}

.ui.center {
    background-color: rgba(0, 0, 0, 0.95);
    color: #fff;
    padding: 2vw;
    border-radius: 0.5vw;
    z-index: 5000;
    width: 32vw;
    position: absolute;
    right: 32vw;
}

